import {api} from "../../api/api";
const TOGGLE_LOADING = 'TOGGLE_LOADING';
const SET_CURRENT_USER = 'SET_CURRENT_USER';
const SET_ERROR_MESSAGE = 'SET_ERROR_MESSAGE';
const SET_IS_SUCCESSFUL = 'SET_IS_SUCCESSFUL';
const RESET = 'RESET';

const initialState = {
    isLoading: false,
    isSuccessful: false,
    user: null,
    errorMessage: null,
};

export const signReducer = (state=initialState, action) => {
    switch(action.type){
        case TOGGLE_LOADING:
            return {...state, isLoading: !state.isLoading};
        case SET_CURRENT_USER:
            return {...state, user: action.user};
        case SET_ERROR_MESSAGE:
            return {...state, errorMessage: action.message};
        case SET_IS_SUCCESSFUL:
            return {...state, isSuccessful: true};
        case RESET:
            return {...state, isSuccessful: false, errorMessage: null};
        default:
            return state;
    }
};

export const toggleLoading = () => ({type: TOGGLE_LOADING});
export const setCurrentUser = (user) => ({type: SET_CURRENT_USER, user});
export const setErrorMessage = (message) => ({type: SET_ERROR_MESSAGE, message});
export const setIsSuccessful = () => ({type: SET_IS_SUCCESSFUL});
export const reset = () => ({type: RESET});

export const signOut = () => async dispatch => {
    dispatch(toggleLoading());
    const response = await api.logOut();
    if (response.code === 200) {
        dispatch(toggleLoading());
    }

};

export const resetState = () => async dispatch => {
    dispatch(reset())
};

export const loginUserWithEmail = (email, password) => async dispatch => {
    dispatch(toggleLoading());
    const response = await api.signInWithEmail(email, password);
    console.log(response);
    if (response.code === 200){
        console.log('200');
        dispatch(setCurrentUser(response.user));
        dispatch(setIsSuccessful())
    }else {
        dispatch(setErrorMessage(response.error))
    }
    dispatch(toggleLoading())
};

export const resetPasswordWithEmail = (email) => async dispatch => {
    dispatch(toggleLoading());
    const response = await api.resetPasswordByEmail(email);
    if (response.code === 200){
        dispatch(setIsSuccessful());
    }else {
        dispatch(setErrorMessage(response.error));
    }
    dispatch(toggleLoading());
};

export const registerUserWithEmail = (email, firstName, lastName, password) => async dispatch => {
    dispatch(toggleLoading());
    const response = await api.createUserWithEmail(email, password, firstName, lastName);
    if (response.code === 200){
        dispatch(setCurrentUser(response.user));
        dispatch(setIsSuccessful())
    }else {
        dispatch(setErrorMessage(response.message));
    }
    dispatch(toggleLoading());
};
